*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  overflow: hidden;
  scrollbar-color: blue orange;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

.contenedor-md {
  max-width: 80rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

/** Media Queries **/
h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.nombre-pagina {
  text-align: center;
  text-transform: capitalize;
}

.tagline {
  font-weight: 900;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
    margin-bottom: 2.5rem;
  }
}

.descripcion-pagina {
  text-align: center;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .descripcion-pagina {
    font-size: 1.6rem;
  }
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 6rem;
  font-size: 1rem;
}
@media (min-width: 768px) {
  .formulario .campo label {
    font-size: 1.4rem;
    flex: 0 0 10rem;
  }
}
@media (min-width: 1024px) {
  .formulario .campo label {
    font-size: 1.6rem;
    flex: 0 0 10rem;
  }
}
.formulario .campo input {
  flex: 1;
  padding: 0.8rem;
  border: none;
  font-size: 1.2rem;
  background-color: #d8dadf;
}
@media (min-width: 768px) {
  .formulario .campo input {
    padding: 1rem;
    font-size: 100%;
  }
}

.acciones {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .acciones {
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-start;
  }
}
.acciones a {
  display: block;
  margin-right: 2rem;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}
.acciones a:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .acciones a {
    text-align: left;
    font-size: 1.5rem;
  }
}

.index {
  margin-top: 8rem;
}
.index .nombre-pagina {
  background: linear-gradient(to right, #066981 0%, #8ce5fa 100%);
  color: transparent;
  background-clip: text;
  /*AGREGA COLOR BACKGROUND AL TEXT*/
  font-size: 5rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .index .nombre-pagina {
    font-size: 8rem;
    margin-bottom: 2.5rem;
  }
}
.index input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .index input[type=submit] {
    width: auto;
  }
}
.index input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.index .acciones a {
  background: linear-gradient(to right, #066981 0%, black 100%);
  color: transparent;
  background-clip: text;
}

.crear {
  margin-top: 8rem;
}
.crear .nombre-pagina {
  background: linear-gradient(to right, #b11e5f 0%, #f8d6e5 100%);
  color: transparent;
  background-clip: text;
  /*AGREGA COLOR BACKGROUND AL TEXT*/
  font-size: 5rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .crear .nombre-pagina {
    font-size: 8rem;
    margin-bottom: 2.5rem;
  }
}
.crear input[type=submit] {
  background-color: #DB2777;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .crear input[type=submit] {
    width: auto;
  }
}
.crear input[type=submit]:hover {
  background-color: #b11e5f;
  cursor: pointer;
}
.crear .acciones a {
  background: linear-gradient(to right, #b11e5f 0%, #2e0819 100%);
  color: transparent;
  background-clip: text;
}